import React from 'react';
import Dropzone from 'react-dropzone'
import { 
  Button, Modal, ModalHeader, ModalBody, ModalFooter, Form, FormGroup,
  Label, Input, Col, FormText } from 'reactstrap';

class AddNewsModal extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      modal: false,
      size: 'lg',
      files: []
    };

    this.toggle = this.toggle.bind(this);
    this.send = this.send.bind(this);
    this.onDrop = this.onDrop.bind(this);

  }

  send(event) {
    event.preventDefault();

    let form = document.querySelector('#add-news-form'),
        formData = new FormData(form),
        result = { screenshots: this.state.files };

    for (let entry of formData.entries()) {
      result[entry[0]] = entry[1];
    }

    result = JSON.stringify(result)

    console.log(JSON.parse(result));
  }

  toggle() {
    this.setState({
      modal: !this.state.modal,
      files: []
    });
  }

  onDrop (files) {
    this.setState({
      files
    })
  }

  componentDidMount() {
    let btn = document.querySelector('#create');
    btn.addEventListener('click', this.toggle);
  }

  render() {
    return (
      <Modal isOpen={this.state.modal} toggle={this.toggle} size={this.state.size} className="create-news">
        <ModalHeader toggle={this.toggle} className="graygreen"> </ModalHeader>
        <ModalBody>
          <Form id="add-news-form">
            <FormGroup>
              <Label for="title-field" className="create-news__label">
                <span className="create-news__for-requred">* </span>
                <span className="create-news__title"> </span>
              </Label>
              <Input id="title-field" type="text" name="title_ru" />
            </FormGroup>
            <FormGroup>
              <Label for="text-caption-field" className="create-news__label">
                <span className="create-news__for-requred">* </span>
                <span className="create-news__title"></span>
              </Label>
              <Input id="text-caption-field" type="textarea" name="description_ru" />
            </FormGroup>
            <FormGroup row>
              <Col md={3}>
                <Label className="create-news__label" for="select-category">
                  <span className="create-news__for-requred">* </span>
                  <span className="create-news__title"></span>
                </Label>
                <Input type="select" id="select-category" name="type">
                  <option> </option>
                  <option value="news"></option>
                  <option>2</option>
                  <option>3</option>
                  <option>4</option>
                  <option>5</option>
                </Input>
              </Col>
            </FormGroup>
            <FormGroup>
              <h3 className="create-news__header-3"> </h3>
              <Dropzone
                onDrop={this.onDrop}
                className="create-news__dropzone"
                activeClassName="create-news__dropzone--active">
                <div className="create-news__dropzone-previews">
                  <div className="create-news__dropzone-message clearfix">
                    <div className="create-news__dropzone-icon pull-left">
                      <img className="create-news__dropzone-image" src="http://designsamples.herokuapp.com/ideas/images/download.svg" alt="dropzone-img" />
                    </div>
                    <p className="create-news__dropzone-caption">
                      {!!this.state.files.length ? this.state.files[0].name : "  ,  "}
                    </p>
                  </div>
                </div>
              </Dropzone>
            </FormGroup>
            <FormGroup>
              <h3 className="create-news__header-3"> </h3>
              <Label className="create-news__label" for="video-url">
                <span className="create-news__title">C   YouTube</span>
              </Label>
              <Input id="video-url" type="url" name="youtube_link" />
            </FormGroup>
            <FormGroup row>
              <Col sm={12}>
                <h3 className="create-news__header-3"> </h3>
              </Col>
              <Col sm={3}>
                <Label className="create-news__label">
                  <span className="create-news__for-requred">* </span>
                  <span className="create-news__title"></span>
                </Label>
                <Input type="select" name="country_name_ru">
                  <option></option>
                  <option>1</option>
                  <option>2</option>
                  <option>3</option>
                </Input>
              </Col>
              <Col sm={3}>
                <label className="create-news__label">
                  <span className="create-news__for-requred">* </span>
                  <span className="create-news__title"></span>
                </label>
                <Input type="select" name="city_name_ru">
                  <option></option>
                  <option>1</option>
                  <option>2</option>
                  <option>3</option>
                </Input>
              </Col>
            </FormGroup>
            <FormGroup>
              <h3 className="create-news__header-3"> </h3>
            </FormGroup>
            <FormGroup check>
              <div className="create-news__checkbox">
                <Input id="global-article" type="checkbox" />
                <Label className="create-news__label" for="global-article">
                  <span className="create-news__title"></span>
                </Label>
              </div>
              <FormText color="muted">
                      .
              </FormText>
            </FormGroup>
            <FormGroup>
              <div className="create-news__checkbox">
                <Input id="important-article" type="checkbox" />
                <Label className="create-news__label" for="important-article">
                  <span className="create-news__title"></span>
                </Label>
              </div>
              <FormText color="muted">
                      .
              </FormText>
            </FormGroup>
          </Form>
        </ModalBody>
        <ModalFooter>
          <Button color="primary" onClick={this.send}></Button>
        </ModalFooter>
      </Modal>
    );
  }
}

export default AddNewsModal;


